body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  display: flex;
}

.main {
  flex-basis: 60%;
  display: flex;
  flex-direction: column;
}

.search {
  padding: 10px;
  margin-right: 20px;
}

.search input {
  width: 100%;
  height: 40px;
  font-size: 24px;
  color: #999;
  padding: 10px;
 
}

.map {
  flex-grow: 1;
  height: 100vh;
  top: 0;
  position: -webkit-sticky;
  position: sticky;
}

.flats {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
}

.flat {
    flex-basis: calc(50% - 20px);
    margin: 10px;
    cursor: pointer;
}

.flat :hover{
    opacity: 0.7;
}


.flat-picture {
    height: 200px;
    background-size: cover;
    background-position: center;


}

.flat-title {
    margin-top: 10px;
    font-weight: bold;

}

.marker {
    width: 48px;
    background-color: white;
    border: 1px solid #999;
    font-size: 18px;
    border-radius: 2px;
    text-align: center;
    padding: 4px 0 ;

}

.marker.selected {
    background-color: red;
    border-color: black

}
